body {
  padding: 0;
  margin: 0;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 15px;
  line-height: 20px;
  font-weight: 400;
  background-color: #FAFBFC;
  color: #222222;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1366px;
  padding: 0 43px;
  margin: 0 auto;
}

/* SITENAV */

.sitenav {
  padding: 10px 0;
  background-color: #fff;
}

.sitenav--fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}

.sitenav__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.sitenav--fixed .logo__img--symbol {
  display: none;
}

.sitenav--fixed .logo__img--text {
  display: block;
}

.sitenav__toggler {
  display: none;
}

.sitenav__collapse {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-grow: 1;
}

.sitenav__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.sitenav__item + .sitenav__item {
  margin-left: 35px;
}

.sitenav__link {
  display: block;
  padding: 8px;
  text-decoration: none;
  color: #222222;
  transition: color 0.3s ease;
}

.sitenav__link:hover {
  color: #fab00b;
}

.sitenav__signup-link {
  display: block;
  padding: 8px;
  margin-left: auto;
  text-decoration: none;
  color: #fab00b;
  transition: color 0.3s ease;
}

.sitenav__signup-link:hover {
  color: #222222;
}

/* LOGO */

.logo {
  margin-right: 60px;
}

.logo__link {
  display: inline-block;
  vertical-align: middle;
}

.logo__img {
  display: block;
}

.logo__img--text {
  display: none;
}

@media (max-width: 1125px) {
  .sitenav__item + .sitenav__item {
    margin-left: 25px;
  }
}

@media (max-width: 1040px) {
  .container {
    padding: 0 30px;
  }

  .sitenav__collapse {
    margin-right: 50px;
  }

  .logo {
    width: 100%;
    margin: 0 auto 10px;
    text-align: center;
  }
}

@media (max-width: 860px) {
  .sitenav__collapse {
    margin-right: 20px;
  }

  .sitenav__item + .sitenav__item {
    margin-left: 15px;
  }
}

@media (max-width: 775px) {
  .sitenav__toggler {
    order: 1;
    display: block;
    border: 1px solid #fab00b;
    padding: 8px;
    margin-left: 20px;
    border-radius: 3px;
    background-color: transparent;
    cursor: pointer;
    transition: border-color 0.3s ease, color 0.3s ease;
  }

  .sitenav__toggler:hover {
    color: #fab00b;
    border-color: #222;
  }

  .sitenav__collapse {
    display: none;
  }

  .sitenav--open .sitenav__collapse,
  .sitenav--nojs .sitenav__collapse {
    display: block;
    width: 100%;
    order: 1;
    margin-top: 10px;
    margin-right: 0;
  }

  .sitenav__list {
    flex-direction: column;
    align-items: stretch;
  }

  .sitenav__item + .sitenav__item {
    border-top: 1px solid #ccc;
    margin-left: 0;
  }

  .sitenav__link {
    text-align: right;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  .sitenav__signup-link {
    border: 1px solid transparent;
    border-radius: 3px;
    background-color: #fab00b;
    color: #fff;
    transition: color 0.3s ease, border 0.3s ease, background-color 0.3s ease;
  }

  .sitenav__signup-link:hover {
    border: 1px solid #fab00b;
    background-color: #fff;
    color: #fab00b;
  }

  .logo {
    width: auto;
    margin: 0;
  }
}

@media (max-width: 510px) {
  .container {
    padding: 0 20px;
  }

  .sitenav__signup-link {
    display: none;
  }
}